import React from 'react';
import { default as MuiRadio, RadioProps } from '@mui/material/Radio';

const Radio = React.forwardRef<HTMLElement, RadioProps>( ( props, ref ) => {
	return (
		<MuiRadio
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<RadioProps>;

export default Radio;
